.app-header{
    padding-block-start: 73px;
    background-color: $clr-primaryBg;
    font-family: 'regular';
    .main-header{
        color: white;
        .main-header-left{
            width: 376px;
            img{
                width: 160px;
            }
        }
        .main-header-right{
            button{
                font-family: 'regular';
                font-size: 18px;
                width: 153px;
                height: 48px;
                background: transparent;
                border: 2px solid white;
                border-radius: 30px;
                color: white;
            }
        } 
    }
}